
table { width: 100%; }

input {
  border: none;
}

.table thead, .table td { 
  text-align: left; 
  padding: 0.25em;
}

.table tr { 
  border-bottom: 1px solid rgb(100, 100, 100);
}

.table tr {
  text-align: center;
}

.add-row-button {
  height: 45px;
  width: 70px;
  border: none;
  border-radius: 8px;
  background-color: white;
}

.add-row-button:hover {
  background-color: hsl(0, 0%, 95%);
}